<template>
  <div class="dyxx">
    <div class="lndetail" v-html="onlydate.Info"></div>
    <div class="rmzyB">
      <hotzy></hotzy>
    </div>
  </div>
</template>
<script>
export default {
  name: "dyxx",
  data() {
    return {
      pageRouterList: "",
      onlydate: [],
    };
  },
  // mounthed: {},
  mounted() {
    var _this = this;

    _this.getQueryString();
  },
  methods: {
    getQueryString: function () {
      var _this = this;

      let Cid = (_this.pageRouterList = _this.$route.query.cid);

      _this.onlydate = _this.Base.GetSinglePageInfoByCid("", Cid);
    },
  },
};
</script>
<style scoped>
.dyxx {
  width: 1200px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
}
.lndetail {
  width: 900px;
  /* overflow: hidden; */
  /* border: 1px saddlebrown solid; */
}
 .lndetail>>>#fvContentID{
   width: 100%;
  
}
.lndetail #fvContentID p {
  display: flex;
}
.lndetail #fvContentID p span {
  display: block !important;
  width: 100%;
  height: 100%;
  margin: auto;
}
.lndetail>>>#fvContentID img {
  display: block !important;
  max-width: 100%;
  height: auto;
  margin: auto;
  
}
.rmzyB {
  width: 260px;
}
</style>


